<template>
<div>
    <c-top-back></c-top-back>

    <div v-if="!success">
        <c-group>
            <c-input title="" placeholder="" @on-change="validate" required v-model="formData.phone" name="phone" :max='11' is-type="china-mobile"></c-input>
            <c-input title="" placeholder="" @on-change="validate" required v-model="formData.code" name="code" :max="6" class="weui_vcode">
                <c-button-send slot="right" type="primary" :validate="phone_status" @on-send="send_out"></c-button-send>
            </c-input>
        </c-group>
        <c-group>
            <c-input title="" type="password" placeholder="" @on-change="validate" required v-model="formData.password" name="password" :max='20'></c-input>
            <c-input title=""  type="password" placeholder="" @on-change="validate" required v-model="formData.password1" name="password1" :max='20'></c-input>
        </c-group>
        <div class="wrap-pd" style="margin-top:10px">
            <c-button :type="color" :disabled='disabled' text="" @click.native="modify" size="block"></c-button>
        </div>
    </div>
    <div v-else>
       <c-msg status="1" msg="" desc="">
           <c-button slot="btn" type="primary" link="login" size="block" text=""></c-button>
       </c-msg>
    </div>
</div>

</template>
<script>
    import utils from '../../libs/utils.js';
    import md5 from 'md5';
    export default {
        data (){
            return { 
              success:false,                        //
              formData:{
                  phone:'',
                  code:'',
                  password:'',
                  password1:'',
                  sn:''
              },
              phone_status:false,
              code_status:false,
              pass_status:false,
              pass1_status:false,
              color:'default',
              disabled:true
            }
        },
        mounted: function () {
            this.$nextTick(function () {
              utils.loadingHide();
            })
        },
        methods:{
           validate(obj){
              if(obj.name=='phone'){
                  this.phone_status=obj.valid;                   
              }
              if(obj.name=='code'){
                  this.code_status=obj.valid;                   
              }
              if(obj.name=='password'){
                  this.pass_status=obj.valid;                    
              }
              if(obj.name=='password1'){
                  this.pass1_status=obj.valid;               
              }
              if(this.phone_status && this.code_status && this.pass_status && this.pass1_status){
                this.color='primary';
                this.disabled=false;
              }else{
                this.color='default';
                this.disabled=true;
              }
           },
           send_out(){
                let that=this;
                utils.ajax({
                    url: basepath + "/seller/seller/verify",
                    dataType: 'json',
                    type: 'POST',
                    data:{'phone':that.formData.phone},
                    success: function(data){
                        if(data.code=="SUCESS"){
                            that.formData.sn=data.result.sn;
                        }else{
                            that.$vux.alert.show('');
                        }
                    }
                });
            },
            modify(){
              if(!this.disabled){
                  var pa=/^[a-zA-Z]{6,20}$/;
                  if(pa.test(this.formData.password)){
                    this.$vux.alert.show('6-20');
                    return false
                  }
                  var pa1=/^[\~.!@#_\+]{6,20}$/;
                  if(pa1.test(this.formData.password)){
                    this.$vux.alert.show('6-20');
                    return false
                  }
                  var pa2=/^[0-9]{6,20}$/;
                  if(pa2.test(this.formData.password)){
                    this.$vux.alert.show('6-20');
                    return false
                  }
                  var pa3=/^[a-zA-Z0-9\~!.@#_\+]{6,20}$/;
                  if(!pa3.test(this.formData.password)){
                    this.$vux.alert.show('6-20');
                    return false
                  }
                  if(this.formData.password!=this.formData.password1){
                    this.$vux.alert.show('');
                    return false
                  }
                  let that=this;
                  utils.ajax({
                      url: basepath + "/seller/seller/reset",
                      dataType: 'json',
                      type: 'POST',
                      data:{
                        'phone':that.formData.phone,
                        'verfiy_code':that.formData.code,
                        'password':md5(that.formData.password),
                        'sn':that.formData.sn
                      },
                      success: function(data){
                          if(data.code=="SUCESS"){
                              that.success=true;
                          }else{
                              that.$vux.alert.show(data.message);
                          }
                      }
                  });
                }
              }
        },
        components:{
            "cGroup":require('../../components/group/group.vue'),
            "cTopBack":require('../../components/x-top-back/x-top-back.vue'),
            "cInput":require('../../components/input/input.vue'),
            "cButton":require('../../components/button/button.vue'),
            "cButtonSend":require('../../components/x-button-send/x-button-send.vue'),
            "cMsg": require("../../components/x-messages/x-messages.vue")
        }
    }
</script>




